* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
          text-align: center; /* 水平置中 */
          margin: 0.5;
          padding: 0.5;
}

body {
  font-family: "Roboto", sans-serif;
   margin-top: 60px; 
}

a {
  text-decoration: none;
  color: black;
  
}
ul {
  list-style-type: none;
}

.morePicture {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 4rem;
}

/* 更多圖片按鈕 */
.morePicture button {
  padding: 0.5rem 0.75rem;
  font-size: 1.5rem;
  border: none;
  background-color: #84cfed;
  color: white;
  cursor: pointer;
  border-radius: 25px;
}

/* 最上面欄位 */
.topnav {
  position: fixed;       /* 固定在页面顶部 */
  top: 0;
  left: 0;
  width: 100%;           /* 占满页面宽度 */
  height: 60px;          /* 你可以根据需要调整高度 */
  background-color: #70cce1; /* 蓝色背景 */
  z-index: 1000;         /* 确保在最前面显示 */
}

nav ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  margin-right: 2rem;
}

nav ul li {
  padding: 0.5rem 1rem;
  align-items: center;

}

nav ul li a {
  padding: 0.5rem;
  font-size: 18pt;
  color: white;
  border-radius: 25%;

}

.search {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 3rem 0rem;
}

/* search輸入+search按鈕 */
.search input,
.search button {
  font-size: 2rem;
  padding: 0.25rem;
  border: 3px solid #70cce1;
  ;
}

.search input {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}


/* search 按鈕 */
.search button {
  background-color: #70cce1;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  color: white;
}

.search button:active{
  background-color: #46a5d3;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  color: black;
}

/* 下面邊框 */
.footer {
  display: flex;
          justify-content: center;
          align-items: center;
  font-size: 15pt;
  background-color: #84cfed;
  color: white;
  width: 100%; 
  min-height: 10vh;
}

.pictures {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.pictures .picture {
  padding: 1rem;
  width: 400px;
}

.pictures .picture .imageContainer {
  overflow: hidden;
  height: 40vh;
}

.pictures .picture .imageContainer img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.pictures .picture .imageContainer img:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
}
